.navbar-header {
  display: flex;
  width: 1050px;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  color: #042657;
  padding-top: 1%;
  padding-bottom: 1%;
}

.navbar-header-right {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.navbar-ul {
  list-style: none;
  display: flex;
  flex-direction: row;
  margin: 0;
  padding: 0;
  font-size: 17px;
  color: #042657 !important;
}

.navbar-ul a {
  text-decoration: none;
  color: #042657 !important;
}
.navbar-ul a:hover {
  text-decoration: none;
  color: #fd581f !important;
}
.youth {
  width: 15%;
}
.navbar-header-button {
  color: white !important;
  padding: 7px 17px;
  border: none;
  border-radius: 5px;
  background-color: #6543b1;
  text-decoration: none !important;
}
.navbar-header-button:hover {
  text-decoration: none !important;
}

