.post {
  background-color: #6543b1;
  padding: 10px 15px;
  color: white;
  margin-top: -7%;
  margin-left: 5%;
  width: 100%;
}

.post ::after {
  position: absolute;
  content: '';
  height: 2px;
  background-color: white;
  width: 3%;
  margin-left: 8px;
  margin-top: 1%;
}
.postap {
  color: white;
  background-color: #6543b1;
  padding: 7px 17px;
  text-decoration: none;
  border: none;
  border-radius: 5px;
}
.navbar-header-mobile {
  display: none;
}

.ham {
  display: none;
  cursor: pointer;
}

.ham svg {
  height: 15px !important;
  width: 25px !important;
  margin-left: 10px;
}

@media (max-width: 1100px) {
  .ham {
    display: block;
  }
  .navbar-header-mobile {
    display: block;
  }
  .navbar-header {
    display: none;
  }

  .nav-links {
    position: absolute;
    display: block;
    top: 100%;
    color: white;
    transform: translateX(-100%);
    transition: 0.3s;
  }
  .nav-links li {
    color: white;
    padding-top: 50px;
    width: 75%;
  }
  .nav-links a:hover {
    text-decoration: underline;
    transition: 0.4s;
  }
}

