.first-p{
    padding-right: 20px;
}

.form-div{
 display: flex;    
}

@media(max-width:1000px){
    .formContainer{
        width: 75%;
    }
}

@media(max-width:501px){

    .first-p{
        padding-right: 0%;
        margin-bottom: 7%;
    }
}

@media(max-width:500px){
    .formContainer{
        width: 95%;
    }
    .form-div{
        flex-direction: column;
        justify-content: space-around;
    }
    .half-width{
            width: 100% !important;
    }
    .form-div input{
        width:-webkit-fill-available;
    }
}



@media(max-width:400px){
    .formContainer{
        width: 98%;
    }
}

